* {
  font-family: "Nunito", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

input[type=number] {
  -moz-appearance: textfield;
  -webkit-appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=checkbox] {
  transform: scale(2);
}

.stop-scrolling {
  height: 100vh;
  overflow: hidden;
}

.loginSection {
  width: 100%;
  height: 92vh;
  padding: 0 20px;
  background: #e9e9e9;
  display: flex;
  background: radial-gradient(ellipse at left bottom, #941d23 0%, #261448 59%, #111b4b 100%);
}

.loginSection .loginContainer {
  padding: 60px;
  margin: auto;
  width: 100%;
  max-width: 520px;
  min-height: 65vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(ellipse at left bottom, #941d23 0%, #261448 59%, #111b4b 100%);
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
}

.loginSection .loginContainer label {
  margin-top: 20px;
  color: white;
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}

.loginSection .loginContainer input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  letter-spacing: 1px;
}

.loginSection .loginContainer .errorMsg {
  padding-top: 5px;
  color: red;
  font-size: 16px;
}

.loginSection .loginContainer .btnContainer {
  width: 100%;
  padding: 24px 0;
}

.loginSection .loginContainer .btnContainer button {
  border: none;
  outline: none;
  width: 100%;
  padding: 15px 0;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  background: #603bbb;
  cursor: pointer;
}

.loginSection .loginContainer .btnContainer span {
  color: yellow;
  width: 100%;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-left: 5px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
}

.loginSection .loginContainer .btnContainer span:hover {
  color: red;
}

.loginSection .loginContainer .btnContainer p {
  margin: 14px 0 0 0;
  text-align: right;
  color: #fff;
}

.accountSection {
  width: 100%;
  min-height: 100vh;
  background: radial-gradient(ellipse at left bottom, #941d23 0%, #261448 59%, #111b4b 100%);
}

.accountSection nav {
  background: #603bbb;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.accountSection h2 {
  color: #fff;
}

.accountSection button {
  width: 86px;
  padding: 8px 0;
  background: #3a2b58;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

.homeSection {
  min-height: 92vh;
  background: radial-gradient(ellipse at left bottom, #941d23 0%, #261448 59%, #111b4b 100%);
}

.productBg {
  height: 90vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.productBg .productContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.productBg .productContainer .Blue_Nikes__container {
  position: relative;
  padding: 60px;
  width: 80vw;
  height: 80vh;
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: black;
  line-height: 35px;
}

.productBg .productContainer .Blue_Nikes__container::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: url("../photos/sky.jpg") no-repeat center fixed;
  transition: opacity 1s ease-in;
}

.productBg .productContainer .Blue_Nikes__container::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: url("../photos/water.jpg") no-repeat center fixed;
  opacity: 0;
  transition: opacity 0.75s ease-in;
}

.productBg .productContainer .Blue_Nikes__container h2 {
  transform: translateY(50px);
  transition: transform 1.25s;
  position: relative;
  z-index: 2;
}

.productBg .productContainer .Blue_Nikes__container p, .productBg .productContainer .Blue_Nikes__container .callToActionHolder {
  transform: translateX(-50px);
  visibility: none;
  opacity: 0;
  transition: visibility 1.5s, opacity 1.5s, transform 1.5s;
  position: relative;
  z-index: 2;
}

.productBg .productContainer .Blue_Nikes__container .headlineSpan {
  color: #4400ff;
  position: relative;
  z-index: 2;
}

.productBg .productContainer .Blue_Nikes__container .callToActionHolder {
  position: relative;
  z-index: 2;
}

.productBg .productContainer .Blue_Nikes__container .callToActionHolder .cartBtn {
  cursor: pointer;
  font-weight: bold;
  height: 40px;
  width: 150px;
  outline: none;
  background: none;
  font-size: 16px;
  color: red;
  border-radius: 30px;
  margin-top: 5px;
  margin-bottom: 20px;
}

.productBg .productContainer .Blue_Nikes__container .callToActionHolder .cartBtn .cartIcon {
  color: black;
  margin-right: 10px;
}

.productBg .productContainer .Blue_Nikes__container .callToActionHolder .cartBtn:hover {
  box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.8);
}

.productBg .productContainer .Blue_Nikes__container .callToActionHolder .loaderHolder {
  display: none;
  width: 150px;
  height: 40px;
  text-align: center;
  margin-top: 5px;
  padding-top: 3px;
  margin-bottom: 20px;
}

.productBg .productContainer .Blue_Nikes__container .callToActionHolder .loaderHolder .loader {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 5px solid transparent;
  border-top: 5px solid #4400ff;
  border-bottom: 5px solid #4400ff;
  background-color: transparent;
  animation-name: circle;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.productBg .productContainer .Blue_Nikes__container img {
  max-height: 100%;
  margin-left: 5%;
  transform: rotateX(13deg) rotateY(-20deg) rotateZ(10deg);
  box-shadow: 50px 50px 70px -20px rgba(0, 0, 0, 0.8);
  transition: transform 1s, box-shadow 1s;
  z-index: 2;
}

.productBg .productContainer .Blue_Nikes__container:hover::before {
  opacity: 0;
}

.productBg .productContainer .Blue_Nikes__container:hover::after {
  opacity: 1;
}

.productBg .productContainer .Blue_Nikes__container:hover h2 {
  transform: translateY(0px);
}

.productBg .productContainer .Blue_Nikes__container:hover p, .productBg .productContainer .Blue_Nikes__container:hover .callToActionHolder {
  transform: translateX(0px);
  visibility: visible;
  opacity: 1;
}

.productBg .productContainer .Blue_Nikes__container:hover img {
  transform: rotateZ(-5deg);
  box-shadow: none;
}

@media only screen and (max-width: 1200px) {
  .productBg .productContainer .Blue_Nikes__container {
    flex-direction: column;
    line-height: 24px;
  }
  .productBg .productContainer .Blue_Nikes__container h2 {
    margin-bottom: 10px;
  }
  .productBg .productContainer .Blue_Nikes__container .headlineSpan {
    color: #4400ff;
  }
  .productBg .productContainer .Blue_Nikes__container img {
    max-height: 60%;
    margin-left: 0%;
    max-width: 100%;
  }
}

