.mainNavBar {
  background-color: slateblue;
  list-style: none;
  position: sticky;
  top: 0;
  z-index: 9999;
  white-space: nowrap;
  width: 100vw;
}

.mainNavBar .preResize {
  display: flex;
  align-items: center;
  justify-content: space-around;
  max-width: 100vw;
  min-height: 8vh;
}

.mainNavBar .preResize .nav_links {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mainNavBar .preResize .linkHolder {
  text-decoration: none;
  list-style: none;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: .5rem;
  padding: 8px 12px;
  position: relative;
}

.mainNavBar .preResize .linkHolder:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  right: 98%;
  bottom: 0;
  background-color: rgba(128, 28, 33, 0.4);
  transition: all .25s ease;
  transform-origin: left;
  border-radius: 10px;
}

.mainNavBar .preResize .linkHolder .foot-prints-icon {
  margin-right: 10px;
  color: black;
}

.mainNavBar .preResize .linkHolder:hover:after {
  right: 0;
}

.mainNavBar .preResize .linkHolder li {
  display: flex;
  align-items: center;
  width: auto;
}

.mainNavBar .preResize .linkHolder li .preAnimationCartSpan {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainNavBar .preResize .linkHolder li .itemsInCartNumber {
  display: flex;
  justify-content: center;
  align-items: center;
  animation: scale 2s linear;
}

.mainNavBar .preResize .linkHolder .icon {
  max-height: 2.5vh;
  margin-right: .5rem;
}

.mainNavBar .preResize .navToggle {
  display: none;
  height: 30px;
  cursor: pointer;
}

.mainNavBar .preResize .navToggle:hover {
  border: 1px solid transparent;
}

.mainNavBar .afterResize {
  display: none;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.2);
  height: 92vh;
  width: 100%;
}

.mainNavBar .afterResize .nav_links {
  width: 80vw;
  display: flex;
  align-content: center;
  justify-content: space-around;
  border-top: 1px solid rgba(180, 61, 67, 0.8);
  padding-top: 15px;
  padding-bottom: 15px;
}

.mainNavBar .afterResize .nav_links .linkHolder {
  text-decoration: none;
  list-style: none;
  color: white;
  text-align: center;
  padding: 8px 9px;
  position: relative;
}

.mainNavBar .afterResize .nav_links .linkHolder:after {
  position: absolute;
  content: '';
  top: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(128, 28, 33, 0.4);
  transition: all .25s ease;
  border-radius: 50%;
}

.mainNavBar .afterResize .nav_links .linkHolder:hover:after {
  top: 0;
}

.mainNavBar .afterResize .nav_links .linkHolder li {
  display: flex;
  align-items: center;
  width: auto;
}

.mainNavBar .afterResize .nav_links .linkHolder li .preAnimationCartSpan {
  display: flex;
