@keyframes shake {
  10%, 90% {
    transform: translateX(1px);
  }
  20%, 80% {
    transform: translateX(-2px);
  }
  30%, 50%, 70% {
    transform: translateX(3px);
  }
  40%, 60% {
    transform: translateX(-4px);
  }
}

@keyframes scale {
  10% {
    width: 10px;
    transform: scale(1.1);
    border-radius: 50%;
    background-color: 10%;
    border-radius: 50%;
    background-color: rgba(128, 28, 33, 0.4);
    opacity: 0.8;
  }
  25% {
    width: 15px;
    transform: scale(1.2);
    border-radius: 50%;
    background-color: rgba(128, 28, 33, 0.4);
  }
  50% {
    width: 20px;
    transform: scale(1.3);
    border-radius: 50%;
    background-color: rgba(128, 28, 33, 0.4);
    opacity: 1;
  }
  75% {
    width: 15px;
    transform: scale(1.1);
    border-radius: 50%;
    background-color: rgba(128, 28, 33, 0.4);
    opacity: 0.8;
  }
  100% {
    width: 10px;
    transform: scale(1);
    border-radius: 50%;
    opacity: 1;
  }
}

@keyframes circle {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
